#about_home {
    background: linear-gradient(rgba(255, 244, 233, 0.3), rgba(255, 244, 233, 0.3));
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    height: 100vh;
    font-family: 'Heebo', sans-serif;
}
.big{
    position: absolute;
    top: 120px;
    left: 40%;
    font-size: 7vw;
    color: rgb(252, 220, 220);
    z-index: -1;
    font-weight: 900;
}

h2.mb-4{
    font-size: 50px;
    font-weight: 400;
}

#myself{
    border-radius: 20px;
    border: 1px solid rgb(252, 220, 220);
    width: 300px;
    margin-bottom: 40px;
    border-top-left-radius: 40px;
    border-bottom-right-radius: 40px;
  }

  #myselfp{
    font-family: "Poppins", Arial, sans-serif;
    font-size: 16px;
    line-height: 1.8;
    font-weight: 400;
    color: #999999;
    font-family: Poppins,sans-serif;
  }
#self_list{
    display: inline-block;
    padding: 0;
    margin: 0;
    width: 100%;
}
#self_list li{
    list-style: none;
    margin-bottom: 10px;
    font-family: Poppins,sans-serif;
}
#self_list li span:first-child {
    font-weight: 700;
    
    width: 130px;
}

#self_list li span {
    width: calc(100% - 130px);
}
  