#about_home {
    /* background: linear-gradient(rgba(255, 244, 233, 0.3), rgba(255, 244, 233, 0.3)); */
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    height: 100vh;
    font-family: 'Heebo', sans-serif;
}

.project {
    width: 100%;
    height: 285px;
    margin-bottom: 30px;
    position: relative;
    z-index: 0; }
    .project.img-2 {
      height: 600px; }
      @media (max-width: 767.98px) {
        .project.img-2 {
          height: 285px; } }
      .project.img-2 .text {
        max-width: 45%;
        z-index: 1; }
        .project.img-2 .text h3 {
          font-size: 22px; }
          .project.img-2 .text h3 a {
            color: #fff; }
        .project.img-2 .text span {
          color: rgba(255, 255, 255, 0.8);
          text-transform: uppercase;
          letter-spacing: 2px; }
        @media (max-width: 767.98px) {
          .project.img-2 .text {
            max-width: 100%;
            opacity: 1; }
            .project.img-2 .text span {
              color: rgba(255, 255, 255, 0.8); } }
    .project .overlay {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      content: '';
      opacity: 0;
      background: #ffbd39;
      z-index: -1;
      -moz-transition: all 0.3s ease;
      -o-transition: all 0.3s ease;
      -webkit-transition: all 0.3s ease;
      -ms-transition: all 0.3s ease;
      transition: all 0.3s ease; }
      @media (max-width: 767.98px) {
        .project .overlay {
          opacity: .2; } }
    .project .text {
      max-width: 80%;
      z-index: 1;
      opacity: 0;
      -moz-transition: all 0.3s ease;
      -o-transition: all 0.3s ease;
      -webkit-transition: all 0.3s ease;
      -ms-transition: all 0.3s ease;
      transition: all 0.3s ease; }
      .project .text h3 {
        font-size: 20px; }
        .project .text h3 a {
          color: #fff; }
      .project .text span {
        color: rgba(255, 255, 255, 0.8);
        text-transform: uppercase;
        letter-spacing: 2px;
        font-size: 12px;
        font-weight: 600; }
      @media (max-width: 767.98px) {
        .project .text {
          opacity: 1; }
          .project .text span {
            color: rgba(255, 255, 255, 0.8); } }
    .project:hover .overlay, .project:focus .overlay {
      opacity: .9; }
    .project:hover .text, .project:focus .text {
      opacity: 1; }
.img{
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;
}

